// ------------------------------------
// Get JSON from Google
// ------------------------------------
async function fechingData(url) {
	console.time("Fething data time: ");

	try {
		const response = await fetch(url);
		const data = await response.json();
		console.timeEnd("Fething data time: ");
		return data.feed.entry;
	} catch(error) {
		console.error(error);
	}
}

